/**************************** Icon Font *****************************/
@font-face {
    font-family: "icons";
    src: url("../fonts/icons/icons.eot?3b6f4cfe5e05f7ee7815103d866a28de?#iefix") format("embedded-opentype"),
url("../fonts/icons/icons.woff2?3b6f4cfe5e05f7ee7815103d866a28de") format("woff2"),
url("../fonts/icons/icons.woff?3b6f4cfe5e05f7ee7815103d866a28de") format("woff"),
url("../fonts/icons/icons.ttf?3b6f4cfe5e05f7ee7815103d866a28de") format("truetype"),
url("../fonts/icons/icons.svg?3b6f4cfe5e05f7ee7815103d866a28de#icons") format("svg");
}

[class*="icon-"]:before {
    font-family: icons !important;
    font-style: normal;
    font-weight: normal !important;
    vertical-align: top;
    line-height: 1;
}

.icon-arrow-right:before {
    content: "\f101";
}
.icon-chevron-down:before {
    content: "\f102";
}
.icon-chevron-up:before {
    content: "\f103";
}
